import { Component, Input, OnInit } from '@angular/core';
import { AlertController } from '@ionic/angular';
import { ModalController } from '@ionic/angular';
import { IProduct } from '../data/iproduct';
import { ProductsService } from '../products.service';

@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.component.html',
  styleUrls: ['./add-product.component.scss'],
})
export class AddProductComponent implements OnInit {
  @Input() code: string;
  @Input() public product: IProduct;
  protected update = false;
  constructor(public modalController: ModalController, public productsService: ProductsService) { }

  async ngOnInit() {
    if (this.product) {
      this.update = true;
      return;
    }
    this.product = {
      nutriments: {
        carbohydrates: null,
        'energy-kcal': null,
        energy: null,
        fat: null,
        proteins: null,
        sugars: null
      },
      addDate: new Date(),
      product_name: '',
      image_url: null,
       // tslint:disable-next-line: no-bitwise
      id: Math.random() * 1e4 | 0};
    if (this.code) {
      const product = await this.productsService.getProductDetails(this.code);
      this.product = product;
    }
  }

  async confirmAdding() {
    if (this.update) {
      this.productsService.updateProduct(this.product);
      console.log(this.product.addDate);
    }
    else {
      this.productsService.addProduct(this.product);
    }
    this.dismissModal();
  }

  dismissModal() {
    if (this.modalController) {
      this.modalController.dismiss();
    }
  }

}
